.loginScrWrapper {
    background-color: #f9f9f9;
    position: relative;
    overflow: hidden;
    min-height: 100vh;
    .site_logo{
        img{
            // filter: invert(1);
            width: 200px;
        }
    }
    .wrapper {
        .primary_heading_2 {
            margin-top: 20%;
            color: $color-seconday;
            .color_primary{
                color: #717374;
            }
        }
        .img_wrapper {
            position: absolute;
            bottom: -25px;
            background: #fff;
            padding: 10px 60px;
            border-radius: 48px;
            width: 460px;
            @media (max-width: 992px) {
                width: 333px;
                left: 86px;
                bottom: -10px;
            }
        }
    }
    .login_form_wrapper {
        padding: 60px 40px;
        border: 1px solid $gray_bg;
        border-radius: 8px;
        background: #fff;
        margin-bottom: 85px;
        @media (min-width: 768px) {
            padding: 62px 30px;
        }
        @media (max-width: 768px) {
            margin-top: 40px;
        }
        .tab_button {
            margin-bottom: 35px;
            &.ext_mrgn {
                margin-bottom: 60px;
            }
            .tab_ele {
                &:not(:last-child) {
                    margin-right: 20px;
                }
                a {
                    font-weight: 700;
                    font-size: em(24);
                    line-height: 33px;
                    color: $color-seconday;
                    &.right_border {
                        position: relative;
                        &:before {
                            content: "";
                            position: absolute;
                            top: 0;
                            right: -60%;
                            background: #00000030;
                            width: 2px;
                            height: 100%;
                        }
                    }
                    &:not(.active) {
                        color: #acacac;
                    }
                }
            }
        }
        .form_heading {
            font-weight: 600;
            font-size: em(28);
            line-height: 150%;
            margin-bottom: 45px;
            color: #000;
        }
        .verify_heading {
            font-family: "Roboto", sans-serif;
            font-weight: 700;
            font-size: em(26);
            color: $border-side-nav;
            margin-top: 55px;
            margin-bottom: 25px;
        }

        .otp_form {
            .otp_field {
                margin: 25px 0;
                .otp_wrapper {
                    width: fit-content;
                    margin: auto;
                    input {
                        width: 60px;
                        height: 55px;
                        font-family: "Roboto", sans-serif;
                        color: $border-side-nav;
                        border: 2px solid #{$border-side-nav}33;
                        border-radius: 15px;
                        text-align: center;
                        font-size: em(24);
                        font-weight: 700;
                    }
                }
            }
            .resend_code {
                font-family: "Roboto", sans-serif;
                font-weight: 400;
                color: $border-side-nav;
                font-size: em(14);
                line-height: 16px;
                cursor: pointer;
                margin-bottom: 25px;
                user-select: none;
            }
        }
        .my_form_group {
            margin-bottom: 21px;
            &:not(.last) {
                margin-bottom: 30px;
            }
            label {
                color: #2b2c43;
                font-family: "Noto Sans", sans-serif;
                font-style: normal;
                font-weight: 500;
                font-size: em(16);
                line-height: 22px;
                opacity: 0.56;
            }
            input {
                outline: none;
                width: 100%;
                height: 38px;
                background: #f9faff;
                border: 0.770732px solid #d5dcff;
                border-radius: 3.85366px;
                padding: 8px;
            }
        }
        .forget_password {
            text-align: end;
            margin-bottom: 30px;
            a {
                font-family: "Noto Sans", sans-serif;
                font-weight: 500;
                font-size: em(14);
                line-height: 19px;
                color: #2b2c43;
                opacity: 0.56;
            }
        }
        .submit_btn {
            button {
                padding-top: 12px;
                padding-bottom: 12px;
                font-weight: 700;
                font-size: em(24);
                line-height: 28px;
            }
        }
        .new_passwrod {
            color: $border-side-nav;
            font-family: "Roboto", sans-serif;
            font-weight: 700;
            font-size: em(31);
            margin-bottom: 40px;
        }
        &.new_passwrod_frm {
            .submit_btn {
                margin-bottom: 35px;
                margin-top: 35px;
            }
        }
    }
    .top_right {
        position: absolute;
        top: 86px;
        right: 0;
    }
    .bottom_left {
        position: absolute;
        bottom: 251px;
        left: 0;
    }
    .bottom_right {
        position: absolute;
        bottom: 0;
        right: 25%;
    }
    .password_field {
        position: relative;
        border-radius: 5px;
        overflow: hidden;
        margin-bottom: 25px;
        input {
            width: 100%;
            height: 45px;
            padding: 10px;
            background: #f0f2f6;
            border: none;
            outline: none;
            &::placeholder {
                font-family: "Roboto", sans-serif;
                font-weight: 400;
                font-size: em(16);
                line-height: 19px;
                color: #000000;
                opacity: 0.4;
            }
        }
        i {
            cursor: pointer;
            position: absolute;
            right: 10px;
            transform: translateY(-50%);
            top: 50%;
        }
    }
}
