@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,200&display=swap");
* {
    margin: 0;
    padding: 0;
    outline: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}
html {
    font-size: 14px;
}

body {
    font-family: "Poppins", sans-serif;
    font-size: 0.88rem;
    color: $color-dark;
    letter-spacing: 0.25px;
    transition: $transition;
}

.d-flex {
    display: flex;
}
.align-items-center {
    align-items: center;
}
.align-items-end {
    align-items: end;
}
.justify-content-between {
    justify-content: space-between;
}
.justify-content-center {
    justify-content: center;
}
.h-100 {
    height: 100%;
}

.primary_icon {
    font-size: 20px;
    height: 20px;
    line-height: 20px;
    width: 20px;
    &.round_icon {
        padding: 20px;
        background: $color-seconday-varient;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
    }
}

.relative {
    position: relative;
}

.pad_1 {
    padding: 20px;
}

.color_secondary {
    color: $color-seconday;
}
.color_primary {
    color: $color-primary;
}

.card_title {
    font-size: 1.5rem;
    font-weight: 600;
}
.card_info {
    font-size: 2.25rem;
    font-weight: 800;
    color: $color-white;
}
.card_light {
    color: $color-white;
}

.large_title {
    font-size: 2.5rem;
    font-weight: 800;
    letter-spacing: 0.2px;
    text-transform: capitalize;
    margin-bottom: 15px;
    z-index: 1000;
    width: fit-content;
    @media (min-width: 900px) {
        position: sticky;
        top: 0px;
    }
}

// margin
.mr-1 {
    margin-right: 0.5rem;
}
.ml-1 {
    margin-left: 0.5rem;
}
.ml-2 {
    margin-left: 1rem;
}
.mr-2 {
    margin-right: 1rem;
}
.mb-2 {
    margin-bottom: 1rem;
}
.mb-1 {
    margin-bottom: 0.5rem;
}

// float
.float-right {
    float: right;
}

// outline_btn
.my_outlined_btn .MuiButton-root {
    padding: 8px;
    min-width: unset;
    .MuiButton-startIcon {
        margin: 0;
        svg {
            font-size: 16px;
        }
    }
}

.my-light-theme {
    .card_light {
        color: $card-bg-dark;
    }
}

// hide in mob
.mob_hidden {
    @media (max-width: 900px) {
        display: none !important;
    }
}
// hide in desktop

.md_hidden {
    @media (min-width: 900px) {
        display: none !important;
    }
}

// text
.text_italic {
    font-style: italic;
}

.small_btn {
    padding: 7px 5px;
    color: #3c76d2;
    border: 2px solid;
    cursor: pointer;
    border-radius: 5px;
    font-size: 12px;
    font-weight: 600;
    margin-bottom: 15px;
    line-height: 12px;
}

.clear-both {
    clear: both;
}

a {
    color: unset;
    text-decoration: unset;
}
a:hover {
    color: unset;
    text-decoration: unset;
}

// user
.root_wrapper {
    background-color: #fff;
    color: $a_body_color;
}

.theme_btn_1 {
    padding: 10px;
    background-color: $a_palette_2;
    color: white;
    border: none;
    cursor: pointer;
    font-weight: 500;
    transition: $transition;
    border-radius: 5px;
    &:hover {
        background-color: $a_palette_2_hover;
    }
}

// check_box
.my_checkbox {
    border: 1px solid $color-info-dark;
    border-radius: 6px;
    display: flex;
    margin-bottom: 0.125rem;
    input[type="checkbox"] {
        display: none;
        & + label:before {
            border: 2px solid $color-info-dark;
            border-radius: 50px;
            content: "";
            display: inline-block;
            height: 20px;
            left: 15px;
            margin-right: 10px;
            position: absolute;
            top: 50%;
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            width: 20px;
        }
    }
    input[type="checkbox"]:checked + label:before {
        content: "✔";
        font-size: 16px;
        line-height: 5px;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        background: #6559fd;
        color: #fff;
        border-color: transparent;
        z-index: 2;
    }
    input[type="checkbox"]:checked + label::after {
        content: "";
        width: 26px;
        height: 26px;
        background: white;
        left: 12px;
        position: absolute;
        border-radius: 50%;
        z-index: 1;
        border: 2px solid #6559fd;
        top: 50%;
        transform: translateY(-50%);
        @media (max-width: 414px) {
            width: 24px;
            height: 24px;
        }
    }
    label {
        align-items: center;
        display: flex;
        font-size: 1em;
        font-weight: 400;
        line-height: 24px;
        padding: 15px 24px 15px 50px;
        position: relative;
        width: 100%;
    }
}
