.venue_list {
  padding-top:112px;
  .filter_wrapper {
    .tabs-prop {
      padding: 10px 15px;
      border-top: 1px solid #d7d7d7;
      border-bottom: 1px solid #d7d7d7;
      background-color: #fff;
      box-shadow: 0 10px 31px 0 rgb(7 152 255 / 9%);
      margin: 25px 0px;
    }
    .tabs-boxes {
      display: inline-block;
    }
    .select-box-prop option {
      padding: 7px !important;
      font-size: 14px;
    }
    .list-icon {
      color: #768082;
      float: right;
      font-size: 20px;
      margin-top: 2px;
    }
    .tabs-boxes select {
      width: fit-content;
      outline: none;
      border: none;
      padding: 7px 10px;
      color: #768082;
    }
    .range-opt {
      padding: 7px !important;
      border-top: 1px solid #eee !important;
    }
  }
  .venueshave_wrapper {
    .flexcontainer {
      display: flex;
      flex-wrap: wrap;
      flex: 100%;
    }

    .sec-row-econ {
      margin-top: 40px;
    }

    .flex-29 {
      flex: 30%;
      transition: 0.4s all ease;
    }

    .flex-29i {
      flex: 30%;
      margin-left: 30px;
      transition: 0.4s all ease;
    }

    .list-box {
      position: relative;
      height: 230px;
      overflow: hidden;
    }

    .list-prop-img {
      width: 100%;
      height: 230px;
      transition: 0.3s all ease;
    }

    .add-list {
      position: absolute;
      bottom: 15px;
      left: 20px;
      color: #eee;
      font-size: 14px;
    }

    .camera-list {
      position: absolute;
      bottom: 15px;
      right: 20px;
      color: #eee;
      font-size: 14px;
    }

    .location-name {
      color: #fff;
      background-color: #2d60ff;
      font-size: 20px;
      font-weight: bold;
      height: auto;
      min-height: 80px;
      padding: 10px 10px;
    }

    .list-wr-box {
      background-color: #f1f1f1;
      border: 1px solid #d7d7d7;
      height: auto;
      min-height: 210px;
      padding: 15px;
    }

    .list-price {
      color: #2d60ff;
      font-size: 24px;
      font-weight: 600;
    }

    .det-prop-list {
      font-size: 14px;
      margin: 10px 0px 20px;
      color: #768082;
    }

    .clr-list {
      color: #2d60ff;
    }

    .quantity-list {
      display: inline-block;
      font-size: 18px;
      margin-right: 15px;
      color: #768082;
    }

    .detail-btn-dum {
      text-align: right;
    }

    .det-btn-list {
      background-color: #f1bf7f;
      padding: 2px 5px;
      color: #fff;
      font-size: 14px;
      text-decoration: none;
      transition: 0.2s all ease;
      border: 1px solid #f1bf7f;
    }

    .det-btn-list:hover {
      transition: 0.2s all ease;
      text-decoration: none;
      color: #f1bf7f;
      background-color: transparent;
      border: 1px solid #f1bf7f;
    }

    .list-anc-prop,
    .list-anc-prop:hover {
      text-decoration: none;
      color: #768082;
    }

    .flex-29:hover,
    .flex-29i:hover {
      transform: translateY(-4%);
      transition: 0.4s all ease;
      box-shadow: 0 10px 31px 0 rgb(7 152 255 / 9%);
    }

    .flex-29:hover .list-prop-img {
      transform: scale(1.08);
      transition: 0.3s all ease;
    }

    .flex-29i:hover .list-prop-img {
      transform: scale(1.08);
      transition: 0.3s all ease;
    }

    .pagination {
      margin: auto;
      width: 100%;
    }

    .pagination a {
      color: #2d60ff;
      float: left;
      padding: 8px 16px;
      text-decoration: none;
      border: 1px solid rgb(7 152 255 / 9%);
    }

    .pagination a.active {
      background-color: #2d60ff;
      color: white;
    }

    .pagination-box {
      margin: 40px auto 0px;
      width: 100%;
    }
  }
}
