.cart_desc {
    overflow-x: auto;
    table {
        width: 100%;
        thead {
            background: $color-background-dark;
            th {
                color: #fff;
                border-bottom: 3px solid $color-background-dark;
                border-right: 1px solid #ededed;
                font-size: 16px;
                font-weight: 600;
                text-transform: capitalize;
                padding: 10px;
                text-align: center;
            }
        }
        tbody {
            tr {
                border-bottom: 1px solid #ededed;
            }
            td {
                border-right: 1px solid #ededed;
                font-weight: 500;
                text-transform: capitalize;
                text-align: center;
                min-width: 150px;
                padding: 10px;
            }
            td:first-child {
                max-width: 180px;
                overflow: hidden;
                height: 125px;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }
            td:last-child {
                span {
                    border: 1px solid $color-background-dark;
                    padding: 10px 15px;
                    transition: all 0.4s ease-in-out;
                    border-radius: 50%;
                    cursor: pointer;
                    color: $color-background-dark;
                    &:hover {
                        color: white;
                        background-color: $color-background-dark;
                    }
                }
            }
        }
    }
}
