.my_rich_textarea {
    .rdw-option-wrapper {
        &[title="Monospace"] {
            display: none;
        }
        &[title="Superscript"] {
            display: none;
        }
        &[title="Subscript"] {
            display: none;
        }
        &[title="Embedded"] {
            display: none;
        }
        &[title="Link"] {
            display: none;
        }
        &[title="Unlink"] {
            display: none;
        }
        &[title="Image"] {
            display: none;
        }
        &[title="Remove"] {
            display: none;
        }
    }

    .rich_editor {
        background: $color-info-dark;
        color: $card-bg-dark;
        padding: 12px;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        min-height: 100px;
        max-height: 200px;
    }
    .rdw-editor-toolbar {
        background: $color-info-dark;
        border-top-right-radius: 10px;
        border-top-left-radius: 10px;

    }
    .public-DraftStyleDefault-block{
        margin: 0;
    }
}

// theme
.my-light-theme {
    .my_rich_textarea {
        .rich_editor {
            background: $card-bg-dark;
            color: $color-info-dark;
        }
    }
}
