.hero_box_wrapper {
    // background-image: url("../../public/images/hero_bg1.jpg");
    background-size: cover;
    .hero_box {
        background-color: rgba(0, 0, 0, 0.4);
        height: 100vh;
        padding: 20px 10px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .hero-d-flex-box {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        height: 80vh;
    }
    .main-hero-box {
        background-color: #fff;
        border-radius: 10px;
        padding: 15px 10px;
        width: 80%;
        margin: 60px 0px;
    }
    .form-search {
        display: flex;
        align-items: center;
    }
    .input_boxes {
        width: 44%;
    }
    .location_select {
        border: none;
        font-size: 18px;
        font-weight: bold;
        color: grey;
        padding: 10px;
        outline: none;
        width: 95%;
    }
    .searchbtn {
        background-color: #2d60ff;
        font-size: 17px;
        font-weight: 500;
        padding: 10px 15px;
        color: #fff;
        outline: none;
        border: none;
        border-radius: 4px;
        width: 10%;
    }
    .searchbtn:hover,
    .call-wr:hover {
        box-shadow: 0px 0px 20px 3px rgb(7, 152, 255);
    }
    .hero-sect-heading {
        color: #fff;
        // text-shadow: 0px 0px 15px #2d60ff;
        font-size: 56px;
        display: block;
    }
    .hero-last-box {
        color: #fff;
        text-align: center;
        text-shadow: 0px 0px 8px #2d60ff;
    }
    .hero-last-p {
        font-size: 18px;
        font-weight: 500;
        margin-bottom: 5px;
    }
    .call-anc,
    .call-anc:hover {
        text-decoration: none;
        color: #fff;
    }
    .call-wr {
        background-color: #2d60ff;
        padding: 10px 30px;
        font-size: 18px;
        font-weight: 500;
        width: fit-content;
        border-radius: 4px;
    }
    .call-btn-box {
        margin: 20px auto 0px;
        width: fit-content;
    }
}

.featured_wrapper {
    .flexcontainer {
        display: flex;
        flex-wrap: wrap;
        flex: 100%;
    }
    .flexcontainer-wid {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        width: 100%;
    }
    .featured-sect {
        padding: 50px 130px;
    }

    .feature-h3 {
        color: #2756ff;
        font-size: 30px;
        margin-bottom: 20px;
    }

    .flex-3 {
        flex: 32%;
        border: 1px solid #ccc9c9;
        padding: 25px;
    }

    .flex-3i {
        flex: 32%;
        border: 1px solid #ccc9c9;
        padding: 25px;
    }

    .feature-img {
        width: 100%;
        margin-bottom: 20px;
    }

    .feature-name {
        color: #0000ff;
        text-align: center;
        font-size: 1.5rem;
        font-weight: 500;
        margin-bottom: 20px;
        font-family: "Roboto", sans-serif;
    }

    .having-box {
        font-family: "Roboto", sans-serif;
    }

    .flex-4 {
        flex: 45%;
        color: #0000ff;
    }

    .flex-4i {
        flex: 45%;
        color: #0000ff;
        margin-left: 10px;
    }

    .icon-feature {
        display: inline-block;
        border: 3px solid #0000ff;
        text-align: center;
        border-radius: 100%;
        width: 38px;
        height: 38px;
        padding-top: 3px;
    }

    .write-feature {
        display: inline-block;
        vertical-align: sub;
        margin-left: 5px;
    }

    .sec-row {
        margin-top: 18px;
    }

    .flex-5 {
        flex: 45%;
    }

    .flex-5i {
        flex: 45%;
        margin-left: 30px;
    }

    .purchase {
        color: #3c4347;
        font-weight: bold;
        font-size: 14px;
    }

    .price-numb {
        color: #41b71e;
        margin-top: 6px;
        font-weight: 500;
    }

    .purchase2 {
        color: #3c4347;
        font-weight: bold;
        font-size: 16px;
    }

    .price-numb2 {
        color: #41b71e;
        margin-top: 6px;
        font-size: 19px;
        font-weight: bold;
    }

    .flex-6 {
        flex: 45%;
    }

    .flex-6i {
        flex: 45%;
        margin-left: 30px;
    }

    .project-rent {
        font-size: 14px;
    }

    .project-numb {
        font-weight: 500;
        margin-top: 6px;
    }

    .project-rent2 {
        font-size: 15px;
    }

    .project-numb2 {
        font-weight: bold;
        margin-top: 6px;
    }

    .border-btm {
        padding: 10px 0px 15px;
        border-bottom: 1px solid #ccc9c9;
    }

    .flex-7 {
        flex: 45%;
    }

    .flex-7i {
        flex: 45%;
    }

    .cont-btn,
    .more-btn {
        width: fit-content;
        margin: auto;
    }

    .more-anc,
    .cont-anc,
    .more-anc:hover,
    .cont-anc:hover {
        text-decoration: none;
        color: #fff;
    }

    .more-wr {
        background-color: #0000ff;
        padding: 8px 20px;
        font-size: 16px;
        font-weight: 500;
        border-radius: 4px;
    }

    .cont-wr {
        background-color: #61ce70;
        border-radius: 4px;
        padding: 8px 20px;
        font-size: 16px;
        font-weight: 500;
    }

    .btn-sect-space {
        justify-content: space-around;
        align-items: center;
        border-bottom: 1px solid #ccc9c9;
        padding: 8px 0px;
    }

    .see-more-btn {
        width: fit-content;
        margin: 30px auto 0px;
    }

    .see-more-anc {
        text-decoration: none;
        color: #fff;
    }

    .see-more-wr {
        background-color: #2756ff;
        color: #fff;
        padding: 15px 25px;
        font-size: 16px;
        font-weight: 500;
    }
}
