.searchItem {
    border: 1px solid lightgray;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
    .siImg {
        width: 200px;
        height: 200px;
        object-fit: cover;
    }
    .siDesc {
        display: flex;
        flex-direction: column;
        gap: 10px;
        flex: 2;
        .siTitle {
            font-size: 20px;
            color: $a_palette_2;
        }
        .siDistance {
            font-size: 12px;
        }
        .siTaxiOp {
            font-size: 12px;
            background-color: #008009;
            color: white;
            width: max-content;
            padding: 3px;
            border-radius: 5px;
        }
        .siSubtitle {
            font-size: 12px;
            font-weight: bold;
        }
        .siFeatures {
            font-size: 12px;
            .package-info {
                font-size: 13px;
                padding-left: 0;
                li {
                    list-style: none;
                    display: inline-block;
                    line-height: 22px !important;
                    vertical-align: 1px;
                    &:after {
                        content: "|";
                        padding-top: 0px;
                        padding-right: 7px;
                        padding-bottom: 0px;
                        padding-left: 10px;
                        color: #cbd4d9;
                        vertical-align: 1px;
                    }
                    &:last-child:after {
                        visibility: hidden;
                        padding: 0 !important;
                        width: 0 !important;
                    }
                }
            }
        }
    }
    .siDetails {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .siRating {
            display: flex;
            justify-content: flex-end;
            align-items: center;
            & > span {
                font-weight: 500;
                margin-right: 8px;
            }
            & > button {
                background-color: $a_palette_2;
                color: white;
                padding: 5px;
                font-weight: bold;
                border: none;
            }
        }
        .siDetailTexts {
            text-align: right;
            display: flex;
            flex-direction: column;
            .siPrice {
                font-size: 24px;
                line-height: 24px;
                margin-bottom: 10px;
            }

            .siTaxOp {
                font-size: 12px;
                color: gray;
            }
        }
    }
}
