.my-dark-theme {
    .serviceCard {
        &.MuiPaper-root {
            background-color: $card-bg-dark !important;
            .MuiMenuItem-root {
                color: $row_color_dark !important;
            }
        }
    }
}

.my-light-theme {
    .MuiPaper-root {
        background-color: $background-light-body !important;
        .MuiMenuItem-root {
            color: $card-bg-dark !important;
        }
    }
}
