.my_checkbox {
  border: 1px solid #ffffffcc;
  border-radius: 6px;
  display: flex;
  margin-bottom: 0.125rem;
}
.my_checkbox input[type=checkbox] {
  display: none;
}
.my_checkbox input[type=checkbox] + label:before {
  border: 2px solid #ffffffcc;
  border-radius: 50px;
  content: "";
  display: inline-block;
  height: 20px;
  left: 15px;
  margin-right: 10px;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 20px;
}
.my_checkbox input[type=checkbox]:checked + label:before {
  content: "✔";
  font-size: 16px;
  line-height: 5px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #6559fd;
  color: #fff;
  border-color: transparent;
  z-index: 2;
}
.my_checkbox input[type=checkbox]:checked + label::after {
  content: "";
  width: 26px;
  height: 26px;
  background: white;
  left: 12px;
  position: absolute;
  border-radius: 50%;
  z-index: 1;
  border: 2px solid #6559fd;
  top: 50%;
  transform: translateY(-50%);
}
@media (max-width: 414px) {
  .my_checkbox input[type=checkbox]:checked + label::after {
    width: 24px;
    height: 24px;
  }
}
.my_checkbox label {
  align-items: center;
  display: flex;
  font-size: 1em;
  font-weight: 400;
  line-height: 24px;
  padding: 15px 24px 15px 50px;
  position: relative;
  width: 100%;
}

