}
@media (max-width: 900px) {
  .body_wrapper .side_nav .nav_wrapper .side_nav_list .side_bar_logo .logo_icon h3 {
    visibility: unset !important;
    opacity: 1;
    transform: translateX(0px);
  }
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .main_menu {
  flex-grow: 1;
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .main_menu .menu-item {
  transition: all 0.15s ease;
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .main_menu .menu-item.expandable_nav {
  border-bottom: 1px solid;
  backdrop-filter: blur(4px);
  cursor: pointer;
  display: block;
  letter-spacing: 0.25px;
  max-width: 100%;
  padding: 20px 24px;
  position: relative;
  text-align: left;
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .main_menu .menu-item.expandable_nav .nav_content .nav_item {
  font-size: 15px;
  font-weight: 500;
  line-height: 15px;
  margin-left: 1rem;
  white-space: nowrap;
  transition: all 0.3s ease-in-out;
  visibility: unset;
}
@media (max-width: 900px) {
  .body_wrapper .side_nav .nav_wrapper .side_nav_list .main_menu .menu-item.expandable_nav .nav_content .nav_item {
    visibility: unset !important;
    opacity: 1;
    transform: translateX(0px);
  }
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .togle_button {
  cursor: pointer;
  display: flex;
  align-items: center;
  flex: none;
  height: 44px;
  padding: 10px 24px;
  position: relative;
  justify-content: end;
  border-top: 1px solid;
}
.body_wrapper .side_nav .nav_wrapper .side_nav_list .togle_button i {
  transition: all 0.3s ease-in-out;
  height: 14px;
}
.body_wrapper .side_nav.togle_side_drawer {
  width: 68px;
}
.body_wrapper .side_nav.togle_side_drawer .menu-item .nav_item {
  transform: translateX(100px);
  opacity: 0;
  visibility: hidden !important;
}
@media (min-width: 900px) {
  .body_wrapper .side_nav.togle_side_drawer .menu-item:after {
    content: attr(data-tooltip);
    position: absolute;
    top: 55%;
    transform: translate(110%, -50%);
    right: 0;
    width: fit-content;
    height: fit-content;
    padding: 5px 10px;
    border-radius: 10px;
    font-size: 10px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.1s ease-in-out;
  }
}
.body_wrapper .side_nav.togle_side_drawer .menu-item:hover:after {
  opacity: 1;
  visibility: unset;
}
.body_wrapper .side_nav.togle_side_drawer .togle_button i {
  transform: rotate(180deg);
}
.body_wrapper .side_nav.togle_side_drawer .side_bar_logo .logo_icon h3 {
  transform: translateX(100px);
  opacity: 0;
  visibility: hidden !important;
}
.body_wrapper .appTopBar {
  height: 52px;
  width: calc(100% - 256px);
  position: fixed;
  top: 0px;
  right: 0;
  padding: 8px 24px;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: end;
  backdrop-filter: blur(20px);
  border-width: 0px 0px thin;
  border-style: solid;
  z-index: 1000;
}
.body_wrapper .appTopBar .left_actions {
  flex-grow: 1;
}
.body_wrapper .appTopBar.togle_top_bar {
  width: calc(100% - 68px);
}
.body_wrapper .body_content {
  width: calc(100% - 256px);
  position: absolute;
  right: 0px;
  top: 52px;
  padding: 20px 20px;
  transition: all 0.3s ease-in-out;
}
.body_wrapper .body_content.togle_side_drawer {
  width: calc(100% - 68px);
}

@media screen and (max-width: 767px) {
  .body_wrapper .side_nav {
    transform: translateX(-100%);
  }
  .body_wrapper .side_nav.togle_side_drawer {
    transform: translateX(0);
    width: 256px;
  }
  .body_wrapper .appTopBar,
.body_wrapper .appTopBar.togle_top_bar,
.body_wrapper .body_content,
.body_wrapper .body_content.togle_side_drawer {
    width: 100%;
  }
  .body_wrapper .body_content .sideNavOverLay::before {
    content: "";
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0px;
    right: 0;
  }
}
