.my_file_upload {
    background: #d0d5db;
    .filepond--root {
        margin-bottom: 0;
    }
    .filepond--item {
        width: 150px;
    }
    .filepond--drip {
        opacity: 1;
        background: #d0d5db;
    }
    .filepond--credits {
        display: none;
    }
    .uploadProfile {
        .filepond--drop-label {
            height: 100%;
            cursor: pointer;
        }
        .filepond--wrapper {
            .filepond--root.filepond--hopper {
                height: 240px !important ;
            }
        }.filepond--list{
            .filepond--item {
                width: 100%;
                height: 210px !important;
            }

        }
    }
}
.old_images_container {
    padding: 0 15px 15px;
    display: flex;
    .uploadedImgPreview {
        width: 150px;
        height: 100px;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        background: #222222;
        border-radius: 8px;
        position: relative;
        margin-right: 6px;
        img {
            max-width: 100%;
            max-height: 100%;
        }
        .imgActions {
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            padding: 10px;
            .removeButton {
                background: #163223;
                color: white;
                border: none;
                outline: none;
                width: 20px;
                height: 20px;
                border-radius: 50%;
                font-size: 10px;
                z-index: 2;
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
            }
            .imgLabel {
                color: white;
                font-size: 10px;
                text-overflow: ellipsis;
                flex: 1;
                overflow: hidden;
                z-index: 2;
            }
            &:before {
                content: "";
                position: absolute;
                top: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-image: linear-gradient(to bottom, rgba(54, 151, 99, 1) 15%, transparent 100%);
                z-index: 1;
            }
        }
    }
}
