.PropertyFilter {
    flex: 1;
    color: #fff;
    padding: 10px;
    border-radius: 10px;
    height: max-content;
    .lsTitle {
        font-size: 20px;
        color: #fff;
        margin-bottom: 10px;
    }
    // & > button {
    //     width: 100%;       
    // }
    .lsItem {
        display: flex;
        flex-direction: column;
        gap: 5px;
        margin-bottom: 10px;
        & > label {
            font-size: 12px;
        }
        & > input {
            height: 30px;
            border: none;
            padding: 5px;
        }
        & > span {
            height: 30px;
            padding: 5px;
            background-color: white;
            color: #000;
            display: flex;
            align-items: center;
            cursor: pointer;
        }
    }
}
