footer {
    padding: 30px 0;
    .site_info {
        .site_logo {

            width: fit-content;
            img{
                filter: invert(1);
            }
            @media (max-width: 768px) {
                margin: auto;
            }
            img {
                margin-bottom: 15px;
            }
            .social_icons {
                .icon_box {
                    a {
                        color: #121212;
                    }
                    &:not(:last-child) {
                        margin-right: 30px;
                    }
                    i {
                        font-size: em(22);
                    }
                }
            }
        }
    }
    .footer_links {
        .typo {
            font-weight: 600;
            font-size: em(16);
            line-height: 24px;
            color: #000000;
            margin-bottom: 24px;
            @media (max-width: 768px) {
                margin-bottom: 15px;
                ine-height: 20px;
            }
        }
        ul {
            padding: 0;
            li {
                a {
                    font-size: em(16);
                    line-height: 24px;
                    font-weight: 400;
                    margin-bottom: 16px;
                    color: #121212;
                }
            }
        }
        .send_msg {
            margin-bottom: 10px;
            height: 48px;
            display: flex;
            align-items: center;
            input[type="text"] {
                border: 1px solid #000;
                padding: 14px 16px;
                width: 220px;
                height: 100%;
                &::placeholder {
                    color: #aaaaaa;
                    font-size: em(14);
                    line-height: 21px;
                }
            }
            button {
                border: none;
                background: #000000;
                color: #fff;
                width: 60px;
                height: 100%;
            }
        }
        .support_text {
            font-size: em(16);
            font-weight: 400;
            color: #000;
            &:not(:last-child) {
                margin-bottom: 10px;
            }
        }
    }
}
